import { Container , Table } from "react-bootstrap";
import "./ResultsStyle.scss";

import { connect } from "react-redux";
import { bindActionCreators } from 'redux';
import LoaderView from "../Common/Loader/Loader";
import {getResultRequest } from "./../../redux/actions/LotteryAction";
import { useState, useEffect } from 'react';
export interface Props {
    resultData : any;
    resultRequest : any;
    loader : boolean;
};
const ResultsMain = ({resultRequest,  resultData, loader}:Props) =>{
    const [result, setResult] = useState(resultData.result ?  resultData.result : []);
    useEffect(() => {
        resultRequest();
    },[]);
    
    console.log(resultData?.result);
    return(
        <>
             {loader && <LoaderView />}
            <div className="results_main" >
                <Container>
                    <h5>Total tickets :  <span>{resultData?.totalTicket}</span></h5>
                    <h5>Unique Wallets :  <span>{resultData?.totalAcc}</span></h5>
                    <Table striped bordered hover variant="dark">
                        <thead>
                            <tr>
                                <th>(INDEX)</th>
                                <th>WINNER</th>
                                <th>DISCORD ID</th>
                                <th>TWITTER ID</th>
                                <th>NO OF TICKETS</th>
                            </tr>
                        </thead>
                        <tbody>
                        {resultData?.result && resultData.result.map((d: any, i: any) => (
                            <tr key={i}>
                                <td>{i+1}</td>
                                <td>{d.walletId}</td>
                                <td>{d.discordID}</td>
                                <td>{d.twitterID}</td>
                                <td>{d.tickets}</td>
                            </tr>
                            ))}
                           
                        </tbody>
                    </Table>
                </Container>
            </div>
        </>
    );
}
const mapStateToProps = ({ lottery }: any) => ({
    loader : lottery.loading,
    resultData : lottery.ticketResult,
});

const mapDispatchToProps = (dispatch: any) => {
    return bindActionCreators({
        resultRequest: getResultRequest
    }, dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(ResultsMain)