import { call, put, takeEvery } from 'redux-saga/effects';
import * as types from '../actions/types';
import * as LotteryServices from '../services/LotteryServices';
import * as LotteryAction from '../actions/LotteryAction';


export function* getLiveRafflesListRequest(action: any): Generator<any> {
    try {
      const params = action.payload;
      const res: any = yield call(LotteryServices.getLiveRafflesList,params);
     
      if (res.data) {
        yield put(LotteryAction.getLiveRafflesListSuccess(res.data));
      } else {
        console.log(res);
        yield put(LotteryAction.getLiveRafflesListFail(res));
      }
    } catch (error : any) {
      yield put(LotteryAction.getLiveRafflesListFail(error)) ;
    }
  }
