  export default function* watchAuthRequests() {
    yield takeEvery(types.GET_LIVE_RAFFLES_REQUEST, getLiveRafflesListRequest);
    yield takeEvery(types.GET_UPCOMING_RAFFLES_REQUEST, getUpcomingRafflesListRequest);
    yield takeEvery(types.GET_PAST_RAFFLES_REQUEST, getPastRafflesListRequest);
    yield takeEvery(types.JOIN_TICKET_REQUEST, joinTicketRequest);
    yield takeEvery(types.CHECK_LEFT_TICKET_REQUEST, checkLeftTicketRequest);
    yield takeEvery(types.GET_RESULT_REQUEST, getResultRequest);
  }
