import * as types from './types';


export const getLiveRafflesListRequest = (data : any) => {
    return async (dispatch: any) => {
      dispatch({
        type: types.GET_LIVE_RAFFLES_REQUEST,
        payload: {data}
      });
    };
  };
  
  export const getLiveRafflesListSuccess = (liveRaffles : any) => {
    return {
        type: types.GET_LIVE_RAFFLES_SUCCESS,
        payload: { liveRaffles }
    }
  }

  export const getLiveRafflesListFail = (message: string) => ({
    type: types.GET_LIVE_RAFFLES_FAIL,
    payload: { message }
  });


