import "./LotteryListStyle.scss";
import lottery from "../../../assets/images/common/01.png";
import plus from "../../../assets/images/lottery/plus.svg";
import minus from "../../../assets/images/lottery/minus.svg";
import { API_URL } from "../../../config";
import LoaderView from "../Loader/Loader";
import { useEffect, useState } from "react";
import { Modal, Alert } from 'antd';
import { connect } from "react-redux";
import { joinTicketRequest, checkLeftTicketRequest } from "./../../../redux/actions/LotteryAction";
import { bindActionCreators } from 'redux';
import { PublicKey, Transaction } from "@solana/web3.js";
import {
  getAssociatedTokenAddress,
  createAssociatedTokenAccountInstruction,
  createTransferInstruction
} from '@solana/spl-token';
import useWalletConnectonStore from "../../..//zustand/WalletConnection";

import * as web3 from '@solana/web3.js';
import { checkLeftRequest } from "../../../redux/services/LotteryServices";
type DisplayEncoding = "utf8" | "hex";
type PhantomEvent = "disconnect" | "connect" | "accountChanged";
type PhantomRequestMethod =
  | "connect"
  | "disconnect"
  | "signTransaction"
  | "signAllTransactions"
  | "signMessage";

interface ConnectOpts {
  onlyIfTrusted: boolean;
}

interface PhantomProvider {
  publicKey: PublicKey | null;
  isConnected: boolean | null;
  signTransaction: (transaction: Transaction) => Promise<Transaction>;
  signAllTransactions: (transactions: Transaction[]) => Promise<Transaction[]>;
  signMessage: (
    message: Uint8Array | string,
    display?: DisplayEncoding
  ) => Promise<any>;
  connect: (opts?: Partial<ConnectOpts>) => Promise<{ publicKey: PublicKey }>;
  disconnect: () => Promise<void>;
  on: (event: PhantomEvent, handler: (args: any) => void) => void;
  request: (method: PhantomRequestMethod, params: any) => Promise<unknown>;
}
export interface Props {
  data?: any;
  checkRequest: any;
  joinRequest: any;
  loader: boolean;
  checkRes: any,
  joinRes: any;
}

const LotteryList = ({ data, checkRequest, joinRequest, loader, checkRes, joinRes }: Props) => {
  const [ticket, setTicket] = useState(1);
  const [discord, setDiscord] = useState("");
  const [twitter, setTwitter] = useState("");
  const [discordError, setDiscordError] = useState("");
  const [twitterError, setTwitterError] = useState("");
  const [isDisabled, setIsDisabled] = useState(false);
  const [totlaNoia, setTotlaNoia] = useState<Number>(data.cost);
  const [payLoader, setPayLoader] = useState(false);
  const [joinedData, setJoinedData] = useState({
    tickets: 0,
    remain: 0,
    total: 0
  })
  const mintPublicKey = new web3.PublicKey('BnV3XcZUbNsuonNKqkQrZSvCN8tVYTJtDgfUx6DJ9riy');
  const recieverWallet = new web3.PublicKey("7hV7vNGFPn1hdp7NbbgSkSksQm21baW4pKsK8f3Z9Hj4");

  const { wallet } = useWalletConnectonStore((state: any) => state)
  var connection = new web3.Connection(
    web3.clusterApiUrl('mainnet-beta'),
  );

  const minusTicket = () => {

    if (ticket > 1) {
      setTicket(ticket - 1);
      setTotlaNoia(data.cost * (ticket - 1))
      // checkRequest({
      //     walletId:wallet.publicKey,
      //     ticketId: data.id,
      //     noOfTicket: ticket -1,
      // });
    }
  }
  const plusTicket = () => {
    if (ticket < data.maxPurchase) {
      setTicket(ticket + 1);
      setTotlaNoia(data.cost * (ticket + 1))
      // checkRequest({
      //     walletId:wallet.publicKey,
      //     ticketId: data.id,
      //     noOfTicket: ticket + 1,
      // });
    }

  }
  // SOCIAL POPUP START
  const [isModalVisible, setIsModalVisible] = useState(false);
  const showModal = () => {
    setIsDisabled(false);
    checkRequest({
      walletId: wallet.publicKey,
      ticketId: data.id,
      noOfTicket: ticket,
    });
    setIsModalVisible(true);
  };
  const handleOk = () => {
    setIsModalVisible(false);
  };
  const handleCancel = () => {
    setIsModalVisible(false);
  };
  useEffect(() => {
    if (wallet.publicKey) {
      checkLeftRequest({
        data: {
          walletId: wallet.publicKey,
          ticketId: data.id,
          noOfTicket: ticket
        }
      }).then((data: any) => {
        setJoinedData(data.data);
      });
    }
  }, [wallet]);
  const getProvider = (): PhantomProvider | undefined => {
    if ("solana" in window) {
      // @ts-ignore
      const provider = window.solana as any;
      if (provider.isPhantom) return provider as PhantomProvider;
    }
  };

  const getSplTokenTransferInstructions = async () => {
    const publicKey = new web3.PublicKey(wallet.publicKey);

    const associatedFromTokenAddr = await getAssociatedTokenAddress(
      mintPublicKey,
      publicKey
    );

    const senderAccount = await connection.getAccountInfo(associatedFromTokenAddr);
    if (senderAccount === null) {
      return [];
    }

    let instructions = [];

    const associatedDestinationTokenAddr = await getAssociatedTokenAddress(
      mintPublicKey,
      recieverWallet
    );

    const receiverAccount = await connection.getAccountInfo(associatedDestinationTokenAddr);
    if (receiverAccount === null) {
      instructions.push(
        createAssociatedTokenAccountInstruction(
          publicKey,
          associatedDestinationTokenAddr,
          recieverWallet,
          mintPublicKey,
        )
      )
    }

    instructions.push(
      createTransferInstruction(
        associatedFromTokenAddr,
        associatedDestinationTokenAddr,
        publicKey,
        totlaNoia.valueOf() * web3.LAMPORTS_PER_SOL,
      )
    );

    return instructions;
  }

  const makeTransactionWithSplToken = async () => {
    const instructions = await getSplTokenTransferInstructions();
    const signature = await confirmTransaction(instructions);
    setPayLoader(false);
    setIsModalVisible(false);
    //Signature or the txn hash
    console.log("Signature: ", signature);
    if (signature !== false) {
      joinRequest({
        walletId: wallet.publicKey,
        ticketId: data.id,
        noOfTicket: ticket,
        discordID: discord,
        twitterID: twitter,
        tx: signature
      }).then((data: any) => {
        checkLeftRequest({
          data: {
            walletId: wallet.publicKey,
            ticketId: data.id,
            noOfTicket: ticket
          }
        }).then((data: any) => {
          setJoinedData(data.data);
        });
      });
    } else {
      console.log("Faild!");
    }
    setTicket(1);
    setDiscord("");
    setTwitter("");
  }

  const makeTransaction = async () => {
    // @ts-ignore
    const { solana } = window;
    const response = await solana.connect();

    var airdropSignature = await connection.requestAirdrop(
      response.publicKey,
      web3.LAMPORTS_PER_SOL,
    );
    await connection.confirmTransaction(airdropSignature);
    console.log("Airdropped");
    let instructions = [
      web3.SystemProgram.transfer({
        fromPubkey: response.publicKey,
        toPubkey: recieverWallet,
        lamports: web3.LAMPORTS_PER_SOL //Investing 1 SOL. Remember 1 Lamport = 10^-9 SOL.
      }),
    ]

    const signature = await confirmTransaction(instructions);
    setPayLoader(false);
    setIsModalVisible(false);
    //Signature or the txn hash
    console.log("Signature: ", signature);
    joinRequest({
      walletId: wallet.publicKey,
      ticketId: data.id,
      noOfTicket: ticket,
      discordID: discord,
      twitterID: twitter
    });
    setTicket(1);
    setDiscord("");
    setTwitter("");
  }

  const confirmTransaction = async (instructions: web3.TransactionInstruction[]) => {
    if (instructions.length === 0) {
      return false;
    }

    const provider = getProvider();
    // @ts-ignore
    const { solana } = window;
    const response = await solana.connect();
    const transaction = new Transaction().add(...instructions);
    const blockHash = await connection.getRecentBlockhash()
    transaction.feePayer = response.publicKey;
    transaction.recentBlockhash = await blockHash.blockhash
    const signed = await provider?.signTransaction(transaction)

    if (signed) {
      const signature = await connection.sendRawTransaction(signed.serialize())
      const result = await connection.confirmTransaction(signature);

      if (result.value.err === null) {
        return signature;
      } else {
        console.log(result.value.err)
        return false;
      }
    }
    return false;
  }

  const submitHandler = () => {
    let isValid = true;

    // if (!new RegExp("^([a-zA-Z ]){2,30}$").test(discord)) {
    //   setDiscordError("Please Enter Valid Id");
    //   isValid = false;
    // }
    if (discord.trim() === '') {
      setDiscordError("Please Enter Discord Id");
      isValid = false;
    }
    // if (!new RegExp("^([a-zA-Z #_]){2,30}$").test(twitter)) {
    //   setTwitterError("Please Enter Valid Id");
    //   isValid = false;
    // }
    if (twitter.trim() === '') {
      setTwitterError("Please Enter witter Id");
      isValid = false;
    }
    if (checkRes?.success && isValid) {
      setPayLoader(true);
      // makeTransaction();
      makeTransactionWithSplToken();
    }
  }
  useEffect(() => {
    setIsDisabled(checkRes?.success ? false : true);
  }, [checkRes]);

  useEffect(() => {
    checkLeftRequest({
      data: {
        walletId: wallet.publicKey,
        ticketId: data.id,
        noOfTicket: ticket
      }
    }).then((data: any) => {
      setJoinedData(data.data);
    });
  }, [joinRes])

  return (
    <>
      {payLoader && <LoaderView />}
      <div className="lotterylist_main_common" >
        <img src={API_URL + "tickets/" + data.image} alt="lottery" className="lottery_img" />
        <div className="usdt_nft" >
          <p>{data.cost} NOIA = 1 TICKET</p>
          <p>{data.nft}  WL SPOTS</p>
        </div>
        <div className="usdt_nft" >
          <p>START DATE: {(new Date(data.startDate)).toLocaleDateString()}</p>
          <p>END DATE: {(new Date(data.endDate)).toLocaleDateString()}</p>
        </div>
        <div className="usdt_nft" >
          <p>ENTRIES: {joinedData.tickets}</p>
          <p>TOTAL: {joinedData.total}</p>
        </div>
        <div className="lotteryname_status" >
          <h5>{data.title}</h5>

        </div>
        <div className="select_lotterypurchase" >
          <button><img src={minus} alt="plus" onClick={minusTicket} /></button>
          <span>{ticket}</span>
          <button><img src={plus} alt="plus" onClick={plusTicket} /></button>
        </div>
        <button className="purchase_lottery" onClick={showModal}>PURCHASE TICKET</button>
      </div>
      <Modal title="Social Link" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel} className="social_modal" >
        <form>
          <div className="form-group" >
            <label>Enter Discord ID</label>
            <input type="text" value={discord} onChange={(e: any) => { setDiscord(e.target.value); setDiscordError("") }} placeholder="Enter Discord ID" />
            {discordError && <p className="error">{discordError}</p>}
          </div>
          <div className="form-group" >
            <label>Enter Twitter ID</label>
            <input type="text" value={twitter} onChange={(e: any) => { setTwitter(e.target.value); setTwitterError("") }} placeholder="Enter Twitter ID" />
            {twitterError && <p className="error">{twitterError}</p>}
          </div>

          <button type="button" className="pink_button" disabled={isDisabled} onClick={submitHandler} >{isDisabled ? "Ticket limit is over" : totlaNoia + "  $NOIA PAY FOR PURCHASE"}</button>
        </form>
      </Modal>
    </>
  );
}
const mapStateToProps = ({ lottery }: any) => ({
  checkRes: lottery.checkRes,
  joinRes: lottery.joinRes,
  loader: lottery.loading,
});

const mapDispatchToProps = (dispatch: any) => {
  return bindActionCreators({
    joinRequest: joinTicketRequest,
    checkRequest: checkLeftTicketRequest,
  }, dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(LotteryList)