import API from './Api';
export const getLiveRafflesList = (params: any) => {
  return API.get(`/front/live-tickest`);
};

export const getUpcomingRafflesList = (params: any) => {
  return API.get(`/front/upcomming-tickest`);
};
export const getPastRafflesList = (params: any) => {
  return API.get(`/front/end-tickest`);
};

export const joinRequest = (params: any) => {
  const formData = new FormData();
  formData.append('walletId', params.data.walletId);
  formData.append('discordID', params.data.discordID);
  formData.append('twitterID', params.data.twitterID);
  formData.append('ticketId', params.data.ticketId);
  formData.append('noOfTicket', params.data.noOfTicket);
  formData.append('tx', params.data.tx);
  return API.post('/front/join-user', formData);
};
export const checkLeftRequest = (params: any) => {
  const formData = new FormData();
  formData.append('walletId', params.data.walletId);
  formData.append('ticketId', params.data.ticketId);
  formData.append('noOfTicket', params.data.noOfTicket);
  return API.post('/front/check-ticket', formData);
};

export const getResult = (params: any) => {
  return API.get(`/front/result`);
};