import "./LotteryStyle.scss";
import { Container, Row, Col } from "react-bootstrap";
import LotteryList from "../Common/LotteryList/LotteryList";
import { useState, useEffect } from 'react';
import { connect } from "react-redux";
import { bindActionCreators } from 'redux';
import LoaderView from "../Common/Loader/Loader";
import { Modal, Alert, message } from 'antd';
import { getLiveRafflesListRequest, getUpcomingRafflesListRequest, getPastRafflesListRequest } from "./../../redux/actions/LotteryAction";
export interface Props {
    LiveRafflesListRequest: any;
    upcomingRafflesRequest: any;
    liveRafflesData: any;
    pastRafflesListRequest: any;
    pastRaffles: any,
    upcomingRaffles: any,
    loader: boolean;
    checkRes: any;
    joinRes: any;
};
const LotteryMain = ({
    LiveRafflesListRequest,
    upcomingRafflesRequest,
    liveRafflesData,
    pastRafflesListRequest,
    pastRaffles,
    upcomingRaffles,
    loader,
    checkRes,
    joinRes
}: Props) => {
    useEffect(() => {

        LiveRafflesListRequest();
        upcomingRafflesRequest();
        pastRafflesListRequest();
    }, []);
    useEffect(() => {
        if (checkRes?.success == false) {
            message.error({
                content: checkRes?.msg,
                className: 'custom-class',
                style: {
                    marginTop: '20vh',
                },
            });
        }
    }, [checkRes]);
    useEffect(() => {
        if (joinRes) {
            message.success({
                content: joinRes,
                className: 'custom-class',
                style: {
                    marginTop: '20vh',
                },
            });
        }
    }, [joinRes]);
    return (
        <>
            {loader && <LoaderView />}
            <div className="lottery_main" >
                {/* <img src={background} alt="lottery" className="background_1" /> */}
                <Container>

                    <button type="button" className="pink_button" >RAFFLES</button>
                    {liveRafflesData.length > 0 && <div className="lottery_list lottery_list_live" >

                        <h5>Live</h5>
                        <Row>
                            {liveRafflesData.length > 0 && liveRafflesData.map((d: any, i: any) => (
                                <Col lg={4} md={6} sm={6} key={"l_" + i}>
                                    <LotteryList data={d} />
                                </Col>
                            ))}
                        </Row>
                    </div>}

                    {pastRaffles.length > 0 && <div className="lottery_list lottery_list_ended" >
                        <h5>Ended</h5>
                        <Row>
                            {pastRaffles.length > 0 && pastRaffles.map((d: any, i: any) => (
                                <Col lg={4} md={6} sm={6} key={"p_" + i}>
                                    <LotteryList data={d} />
                                </Col>
                            ))}
                        </Row>
                    </div>}
                    {upcomingRaffles.length > 0 && <div className="lottery_list lottery_list_upcoming" >
                        <h5>Upcoming</h5>
                        <Row>
                            {upcomingRaffles.length > 0 && upcomingRaffles.map((d: any, i: any) => (
                                <Col lg={4} md={6} sm={6} key={"u_" + i}>
                                    <LotteryList data={d} />
                                </Col>
                            ))}
                        </Row>
                    </div>}
                </Container>
            </div>
        </>
    );
}
const mapStateToProps = ({ lottery }: any) => ({
    liveRafflesData: lottery.liveRaffles,
    pastRaffles: lottery.pastRaffles,
    upcomingRaffles: lottery.upcomingRaffles,
    loader: lottery.loading,
    checkRes: lottery.checkRes,
    joinRes: lottery.joinRes,
});

const mapDispatchToProps = (dispatch: any) => {
    return bindActionCreators({
        LiveRafflesListRequest: getLiveRafflesListRequest,
        upcomingRafflesRequest: getUpcomingRafflesListRequest,
        pastRafflesListRequest: getPastRafflesListRequest
    }, dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(LotteryMain)