import * as types from './../actions/types';

const initialState: any = {
  liveRaffles: [],
  upcomingRaffles: [],
  pastRaffles: [],
  joinRes: null,
  checkRes: null,
  ticketResult: []
};

const reducer = (state = initialState, action: any) => {
  switch (action.type) {
    case types.GET_LIVE_RAFFLES_REQUEST:
      return { ...state, ...action.payload, loading: true, liveRaffles: [] }
    case types.GET_UPCOMING_RAFFLES_REQUEST:
      return { ...state, ...action.payload, loading: true, upcomingRaffles: [] }
    case types.GET_PAST_RAFFLES_REQUEST:
      return { ...state, ...action.payload, loading: true, pastRaffles: [] }
    case types.JOIN_TICKET_REQUEST:
      return { ...state, ...action.payload, loading: true, joinRes: null, checkRes: null }
    case types.CHECK_LEFT_TICKET_REQUEST:
      return { ...state, ...action.payload, loading: true, checkRes: null }
    case types.GET_RESULT_REQUEST:
      return { ...state, ...action.payload, loading: true, ticketResult: [] }
    case types.CHECK_LEFT_TICKET_SUCCESS:
      const checkedInfo = action.payload;
      console.log("==============================")
      console.log(checkedInfo)
      return { ...state, ...action.payload, loading: false };
    case types.GET_LIVE_RAFFLES_SUCCESS:
    case types.GET_LIVE_RAFFLES_FAIL:
    case types.GET_UPCOMING_RAFFLES_SUCCESS:
    case types.GET_UPCOMING_RAFFLES_FAIL:
    case types.GET_PAST_RAFFLES_SUCCESS:
    case types.GET_PAST_RAFFLES_FAIL:
    case types.JOIN_TICKET_SUCCESS:
    case types.JOIN_TICKET_FAIL:
    case types.CHECK_LEFT_TICKET_FAIL:
    case types.GET_RESULT_SUCCESS:
    case types.GET_RESULT_FAIL:
      return { ...state, ...action.payload, loading: false };
    default:
      return state;
  }
};

export default reducer;